.form {
    height: 330px;
    width: 350px;
    background-color: #dbe9ef;
    border-radius: 15px;
    padding: 0px 20px 20px 20px;

}
.header {
    text-align: center;
    margin: 15px auto 0;
    font-weight: bold !important;
    font-size: 20px !important;
    font-family: 'Alike';
}
.subheader {
    text-align: center;
    font-size: 15px !important;
    font-family: 'Alike';
}
.input_field {
    width: 100%;
    margin-top: 10px;
}
.main_form {
    display: flex;
    justify-content: center;
    height: 100%;
    position: relative;
    top: 20%;
}
.signin{
    margin-top: 10px;
}
.header {
    font-size: 22px;
    font-weight: 600;
}
.subheader {
    font-size: 12px;
}
.main-box {
    height: 100vh;
    overflow: hidden;
    background-color: #e7f1ea;

}
.textfield {
    width: 80%;
    margin: 9px 20px !important;
  
}
.capcha_text {
    width: 40%;
    margin: 9px 20px !important;
}
.button {
    background-color: rgba(109, 199, 143, 255) !important;
    width: 90%;
    margin: 9px 20px !important;
    padding: 8px 0px 8px 0px !important;
    font-size: 20px !important;
    font-weight: bold !important;
    color: white !important;
}
.capcha_box {
    display: flex;
}
.capcha {
    margin-top: 10px;
}
.appbar {
    box-shadow: none !important;
}
.image {
    width: 200px;
    margin: 40px 0 0 18px;
}
.MuiOutlinedInput-root {
    height: 45px;
    background-color: #f2f5f7;
    padding-left: 10px;
    border: 1px solid #E2E6EA;
    outline: none;
    border-radius: 15px !important;
}
@media screen and (max-width: 375px) {
    .image {
        margin: 20px 0 0 20px
    }
    .form {
        height: 320px;
        width: 280px;
    }
    .button {
         padding: 6px 0px 6px 0px !important;
    }
}
@media screen and (max-width: 425px) {
    .image {
       width: 150px;
       margin: 30px 0 0 18px
    }
 
}
