.img{
    width: 156px;
    margin: 20px;
}
.drawer{
    background-color: #f3f9f9ba !important;
    height: 100%;
}
.drawer_box{
    background-color: #f3f9f9ba !important;
} 
.listitem:hover{
    color: black;
    cursor: pointer; 
}
.listitem{
    padding-bottom: 5px !important;
    font-size: 15px;
    color: #009d63;
    font-weight: bold;
}
.bar_icon{
    font-size: 30px !important;
    color: #009d63 !important;
    margin-top: 5px;
}
.appbar{
    box-shadow: none !important; 
}
.toolbar{
    justify-content: space-between;
    min-height: 50px !important;
}
.link{
    color: gray !important;
    font-weight: bold;
}
.admin{
    margin-right: 10px;
}
.admin:hover{
    color: black;
    cursor: pointer;

}
.exit{
    cursor: pointer;
}
.links{
    display: flex;
}
.icon{
    margin-right: 7px;
    font-size: 12px;
}
.rightSide .links {
    color: gray;
}
.navbar .rightSide button {
    display: none;
  }
@media only screen and (max-width: 768px) {
    .navbar .rightSide button {
      display: flex !important;
    }
  
    .navbar .rightSide .links {
      display: none;
    }
    .navbar .rightSide #hidden {
      position: absolute;
      left: 20px;
      top: 50px;
      height: 230px;
      max-height: 230px;
    
      display: flex;
      flex-direction: column;
    }
    .navbar .rightSide #hidden a {
      margin: 15px;
      font-size: 20px;
    }
    .admin{
        margin-right: 2px;
        
    }
    .link{
        font-size: 15px !important;
        margin-left: 0px !important;
    }
    .listitem{
          font-size: 13px;
    }
  }